/**
 * Format of User-related data
 *
 * @class User
 */
export class User{
    UserID:number;
    Username:string;
    firstName?:string;
    lastName?:string;
    password:string;
    role: string;
    constructor( UserID:number, Username:string, password:string)
    {
        this.Username=Username;
        this.password=password;
        this.role="user";
        this.UserID=UserID;
    }
}

/**
 * Format of Ingredient-related data
 *
 * @class Ingredient
 */
export class Ingredient{
    name:string;
    quantity:number;
    unit:string;
    optional:boolean;
    constructor( name:string, quantity:number, unit:string, Optional:boolean)
    {
        this.name=name;
        this.quantity=quantity;
        this.unit=unit;
        this.optional=Optional;
    }
}

/**
 * Format of Instruction-related data
 *
 * @class Instruction
 */
export class Instruction{
    StepNo:number;
    description:string;
    constructor( StepNo:number, description:string)
    {
        this.StepNo = StepNo;
        this.description = description;
    }
}

/**
 * Format of Recipe-related data
 *
 * Note that this class contains an array of
 * Ingredients and Instructions
 *
 * @class Recipe
 */
export class Recipe{
    RecipeID:number;
    title:string;
    username:string;
    ingredients:Ingredient[];
    instructions:Instruction[];
    DateAdded:Date;
    constructor(RecipeID:number, title:string, username:string, ingredients:Ingredient[],
                instructions:Instruction[], DateAdded:Date)
    {
        this.RecipeID=RecipeID;
        this.title=title;
        this.username=username;
        this.ingredients=ingredients;
        this.instructions=instructions;
        this.DateAdded=DateAdded;
    }
}

/**
 * Format of Rating-related data
 *
 * @class Rating
 */
export class Rating{
    RecipeID:number;
    Upvotes:number;
    Downvotes:number;
    Rank?:number;
    constructor(RecipeID:number, Upvotes:number, Downvotes:number)
    {
        this.RecipeID=RecipeID;
        this.Upvotes=Upvotes;
        this.Downvotes=Downvotes;
    }
}